import  eggInit from './eggInit.js'
export default {
    name: 'eggIndex',
    data: function(){
        return {
            ruleShow: false
        }
    },
    mounted() {
        
        eggInit();
    },
    methods: {
        close(){
            $("#mask").hide();
            $("#mask .winning").removeClass('reback');
            $("#mask #card").removeClass('pull');
        }
    },
}



